'use client';

import { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { 
  FileText, 
  Plus, 
  Search, 
  Filter, 
  Download,
  Eye,
  Edit,
  Trash2,
  Upload,
  Link,
  Calendar,
  User,
  CheckCircle,
  AlertCircle
} from 'lucide-react';
import { useAuth } from '@/contexts/AuthContext';

interface Evidence {
  id: number;
  title: string;
  description: string;
  type: 'DOCUMENT' | 'SCREENSHOT' | 'POLICY' | 'PROCEDURE' | 'TRAINING_RECORD' | 'AUDIT_REPORT' | 'OTHER';
  filePath?: string;
  fileUrl?: string;
  externalUrl?: string;
  requirementId: number;
  branchId: number;
  uploadedById: number;
  uploadedAt: string;
  status: 'PENDING' | 'APPROVED' | 'REJECTED';
  requirement?: {
    id: number;
    clause: string;
    title: string;
    section: string;
  };
  branch?: {
    id: number;
    name: string;
  };
  uploadedBy?: {
    id: number;
    firstName: string;
    lastName: string;
  };
}

export default function EvidencePage() {
  const [evidence, setEvidence] = useState<Evidence[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterType, setFilterType] = useState('all');
  const [filterStatus, setFilterStatus] = useState('all');
  const { user } = useAuth();

  useEffect(() => {
    fetchEvidence();
  }, []);

  const fetchEvidence = async () => {
    try {
      setLoading(true);
      // TODO: Replace with actual API call
      // const response = await api.get('/evidence');
      // setEvidence(response.data);
      
      // Mock data for now
      setEvidence([
        {
          id: 1,
          title: 'MFA Implementation Policy',
          description: 'Official policy document outlining multi-factor authentication requirements for all user accounts',
          type: 'POLICY',
          filePath: '/documents/mfa-policy.pdf',
          requirementId: 1,
          branchId: 1,
          uploadedById: 1,
          uploadedAt: '2024-01-15T10:00:00Z',
          status: 'APPROVED',
          requirement: {
            id: 1,
            clause: 'A.9.2.1',
            title: 'User access provisioning',
            section: 'Access Control'
          },
          branch: {
            id: 1,
            name: 'Head Office'
          },
          uploadedBy: {
            id: 1,
            firstName: 'John',
            lastName: 'Doe'
          }
        },
        {
          id: 2,
          title: 'Access Review Procedure',
          description: 'Step-by-step procedure for conducting regular user access reviews',
          type: 'PROCEDURE',
          filePath: '/documents/access-review-procedure.pdf',
          requirementId: 2,
          branchId: 1,
          uploadedById: 2,
          uploadedAt: '2024-01-16T14:30:00Z',
          status: 'PENDING',
          requirement: {
            id: 2,
            clause: 'A.9.2.2',
            title: 'User access review',
            section: 'Access Control'
          },
          branch: {
            id: 1,
            name: 'Head Office'
          },
          uploadedBy: {
            id: 2,
            firstName: 'Jane',
            lastName: 'Smith'
          }
        },
        {
          id: 3,
          title: 'Security Training Records',
          description: 'Records of security awareness training completed by all staff members',
          type: 'TRAINING_RECORD',
          externalUrl: 'https://training.company.com/records/security-2024',
          requirementId: 3,
          branchId: 1,
          uploadedById: 1,
          uploadedAt: '2024-01-17T09:15:00Z',
          status: 'APPROVED',
          requirement: {
            id: 3,
            clause: 'A.7.2.2',
            title: 'Information security awareness, education and training',
            section: 'Human Resource Security'
          },
          branch: {
            id: 1,
            name: 'Head Office'
          },
          uploadedBy: {
            id: 1,
            firstName: 'John',
            lastName: 'Doe'
          }
        }
      ]);
    } catch (error) {
      console.error('Error fetching evidence:', error);
    } finally {
      setLoading(false);
    }
  };

  const getTypeInfo = (type: string) => {
    switch (type) {
      case 'DOCUMENT': return { label: 'Document', color: 'bg-blue-100 text-blue-800', icon: FileText };
      case 'SCREENSHOT': return { label: 'Screenshot', color: 'bg-green-100 text-green-800', icon: FileText };
      case 'POLICY': return { label: 'Policy', color: 'bg-purple-100 text-purple-800', icon: FileText };
      case 'PROCEDURE': return { label: 'Procedure', color: 'bg-orange-100 text-orange-800', icon: FileText };
      case 'TRAINING_RECORD': return { label: 'Training Record', color: 'bg-yellow-100 text-yellow-800', icon: FileText };
      case 'AUDIT_REPORT': return { label: 'Audit Report', color: 'bg-red-100 text-red-800', icon: FileText };
      case 'OTHER': return { label: 'Other', color: 'bg-gray-100 text-gray-800', icon: FileText };
      default: return { label: 'Unknown', color: 'bg-gray-100 text-gray-800', icon: FileText };
    }
  };

  const getStatusInfo = (status: string) => {
    switch (status) {
      case 'PENDING': return { label: 'Pending Review', color: 'bg-yellow-100 text-yellow-800', icon: AlertCircle };
      case 'APPROVED': return { label: 'Approved', color: 'bg-green-100 text-green-800', icon: CheckCircle };
      case 'REJECTED': return { label: 'Rejected', color: 'bg-red-100 text-red-800', icon: AlertCircle };
      default: return { label: 'Unknown', color: 'bg-gray-100 text-gray-800', icon: AlertCircle };
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const filteredEvidence = evidence.filter(item => {
    const matchesSearch = item.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         item.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         item.requirement?.title.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesType = filterType === 'all' || item.type === filterType;
    const matchesStatus = filterStatus === 'all' || item.status === filterStatus;
    
    return matchesSearch && matchesType && matchesStatus;
  });

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Evidence</h1>
          <p className="text-gray-600 mt-1">Manage compliance evidence and documentation</p>
        </div>
        <div className="flex gap-2">
          <Button variant="outline" className="flex items-center gap-2">
            <Upload className="h-4 w-4" />
            Upload File
          </Button>
          <Button className="flex items-center gap-2">
            <Plus className="h-4 w-4" />
            Add Evidence
          </Button>
        </div>
      </div>

      {/* Filters */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Filter className="h-5 w-5" />
            Filters
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
               <Input
                 placeholder="Search evidence..."
                 value={searchTerm}
                 onChange={(e) => setSearchTerm(e.target.value)}
                 className="pl-10 border-black text-black"
               />
            </div>
             <Select value={filterType} onValueChange={setFilterType}>
               <SelectTrigger className="border-black text-black">
                 <SelectValue placeholder="Type" />
               </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Types</SelectItem>
                <SelectItem value="DOCUMENT">Document</SelectItem>
                <SelectItem value="SCREENSHOT">Screenshot</SelectItem>
                <SelectItem value="POLICY">Policy</SelectItem>
                <SelectItem value="PROCEDURE">Procedure</SelectItem>
                <SelectItem value="TRAINING_RECORD">Training Record</SelectItem>
                <SelectItem value="AUDIT_REPORT">Audit Report</SelectItem>
                <SelectItem value="OTHER">Other</SelectItem>
              </SelectContent>
            </Select>
             <Select value={filterStatus} onValueChange={setFilterStatus}>
               <SelectTrigger className="border-black text-black">
                 <SelectValue placeholder="Status" />
               </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Status</SelectItem>
                <SelectItem value="PENDING">Pending Review</SelectItem>
                <SelectItem value="APPROVED">Approved</SelectItem>
                <SelectItem value="REJECTED">Rejected</SelectItem>
              </SelectContent>
            </Select>
             <Button variant="outline" onClick={fetchEvidence} className="border-black text-black hover:bg-black hover:text-white">
               Refresh
             </Button>
          </div>
        </CardContent>
      </Card>

      {/* Evidence List */}
      <div className="space-y-4">
        {filteredEvidence.map((item) => {
          const typeInfo = getTypeInfo(item.type);
          const statusInfo = getStatusInfo(item.status);
          const TypeIcon = typeInfo.icon;
          const StatusIcon = statusInfo.icon;
          
          return (
            <Card key={item.id} className="hover:shadow-lg transition-shadow">
              <CardHeader>
                <div className="flex justify-between items-start">
                  <div>
                    <CardTitle className="text-lg">{item.title}</CardTitle>
                    <CardDescription className="text-sm text-gray-600">
                      {item.requirement?.clause} - {item.requirement?.title}
                    </CardDescription>
                  </div>
                  <div className="flex gap-2">
                    <Badge className={typeInfo.color}>
                      <TypeIcon className="h-3 w-3 mr-1" />
                      {typeInfo.label}
                    </Badge>
                    <Badge className={statusInfo.color}>
                      <StatusIcon className="h-3 w-3 mr-1" />
                      {statusInfo.label}
                    </Badge>
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <p className="text-sm text-gray-700">
                    {item.description}
                  </p>
                  
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm">
                    <div className="flex items-center gap-2">
                      <User className="h-4 w-4 text-gray-400" />
                      <div>
                        <span className="text-gray-500">Uploaded by:</span>
                        <div className="font-medium">
                          {item.uploadedBy?.firstName} {item.uploadedBy?.lastName}
                        </div>
                      </div>
                    </div>
                    <div className="flex items-center gap-2">
                      <Calendar className="h-4 w-4 text-gray-400" />
                      <div>
                        <span className="text-gray-500">Uploaded:</span>
                        <div className="font-medium">
                          {formatDate(item.uploadedAt)}
                        </div>
                      </div>
                    </div>
                    <div className="flex items-center gap-2">
                      <FileText className="h-4 w-4 text-gray-400" />
                      <div>
                        <span className="text-gray-500">Section:</span>
                        <div className="font-medium">
                          {item.requirement?.section}
                        </div>
                      </div>
                    </div>
                  </div>
                  
                  <div className="flex gap-2">
                    <Button size="sm" variant="outline" className="flex-1">
                      <Eye className="h-4 w-4 mr-1" />
                      View
                    </Button>
                    {item.filePath && (
                      <Button size="sm" variant="outline" className="flex-1">
                        <Download className="h-4 w-4 mr-1" />
                        Download
                      </Button>
                    )}
                    {item.externalUrl && (
                      <Button size="sm" variant="outline" className="flex-1">
                        <Link className="h-4 w-4 mr-1" />
                        Open Link
                      </Button>
                    )}
                    <Button size="sm" variant="outline" className="flex-1">
                      <Edit className="h-4 w-4 mr-1" />
                      Edit
                    </Button>
                    <Button size="sm" variant="outline" className="text-red-600 hover:text-red-700">
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          );
        })}
      </div>

      {filteredEvidence.length === 0 && (
        <Card>
          <CardContent className="text-center py-12">
            <FileText className="h-12 w-12 text-gray-400 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2">No evidence found</h3>
            <p className="text-gray-600 mb-4">
              {searchTerm || filterType !== 'all' || filterStatus !== 'all'
                ? 'Try adjusting your filters to see more results.'
                : 'Get started by uploading your first piece of evidence.'}
            </p>
            <div className="flex gap-2 justify-center">
              <Button variant="outline">
                <Upload className="h-4 w-4 mr-2" />
                Upload File
              </Button>
              <Button>
                <Plus className="h-4 w-4 mr-2" />
                Add Evidence
              </Button>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
